import React, { useRef, useState } from 'react';
import { useAppDispatch } from '../../../renderer/store/hooks';
import { SIGNING_UP } from '../../../renderer/_reducers/_slices/signupSlice';
import { API_URL } from '../../../renderer/CONSTANT_URL';

export { ConfirmPasswordStage };

function ConfirmPasswordStage({
    move,
    setMove,
}: {
    move: number;
    setMove: React.Dispatch<React.SetStateAction<number>>;
}) {
    const dispatch = useAppDispatch();

    const confirmPassword = useRef<HTMLInputElement>(null);
    const [message, setMessage] = useState('');

    const handlePrev = () => {
        setMove((prev) => prev + 100);
    };

    const handleClick = () => {
        const password = (document.getElementById('signupPassword') as HTMLInputElement).value;

        // ÎπÑÎ∞ÄÎ≤àÌò∏ÏôÄ Í∞ôÏùÄÏßÄ ÌôïÏù∏
        const element = confirmPassword.current;
        if (element !== null) {
            if (element.value === password) {
                setMessage('');

                // ÌÜ±Î∞î Îí§Î°úÍ∞ÄÍ∏∞ Î≥¥Ïó¨Ï£ºÍ∏∞
                dispatch(SIGNING_UP(false));
                // ÌöåÏõêÍ∞ÄÏûÖ ÌÜµÏã†
                signup();
            } else {
                setMessage('ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏùºÏπòÌïòÏßÄ ÏïäÏäµÎãàÎã§.');
            }
        }
    };

    async function signup() {
        try {
            const userId = (document.getElementById('signupId') as HTMLInputElement).value;
            const password = (document.getElementById('signupPassword') as HTMLInputElement).value;
            const nickName = (document.getElementById('signupNickName') as HTMLInputElement).value;

            const body = {
                userId: userId,
                password: password,
                nickName: nickName,
            };

            const res = await fetch(`${API_URL}/users/signup`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(body),
            });

            const data = await res.json();

            if (data.success) {
                alert('üéâ ÌöåÏõêÍ∞ÄÏûÖÏóê ÏÑ±Í≥µÌñàÏäµÎãàÎã§');
                window.location.href = '/login';
            } else {
                alert('ÌöåÏõêÍ∞ÄÏûÖÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§');
                console.error(data.errorMessage);
            }
        } catch (err) {
            console.error(err);
        }
    }

    return (
        <section className="signup-confirm-password-stage" style={move !== -400 ? { visibility: 'hidden' } : {}}>
            <h3 className="sr-only">ÎπÑÎ∞ÄÎ≤àÌò∏ Ïû¨ÏûÖÎ†•</h3>
            <label htmlFor="signupConfirmPassword">ÎπÑÎ∞ÄÎ≤àÌò∏ Ïû¨ÏûÖÎ†•</label>
            <input
                type="password"
                id="signupConfirmPassword"
                ref={confirmPassword}
                placeholder="ÎπÑÎ∞ÄÎ≤àÌò∏ Ïû¨ÏûÖÎ†•"
                minLength={8}
                maxLength={32}
                aria-describedby="desc-signup-confirm-password"
            />
            <em className="txt-signup-message" id="desc-signup-confirm-password">
                {message}
            </em>
            <div className="button-group-move">
                <button type="button" onClick={handlePrev} aria-label="Ïù¥Ï†Ñ ÌôîÎ©¥ÏúºÎ°ú Ïù¥Îèô">
                    ÎπÑÎ∞ÄÎ≤àÌò∏
                </button>
                <button type="button" onClick={handleClick} aria-label="Îã§Ïùå ÌôîÎ©¥ÏúºÎ°ú Ïù¥Îèô">
                    ÏôÑÎ£å
                </button>
            </div>
        </section>
    );
}
