import React, { useRef, useState } from 'react';
import { useAppDispatch } from '../../../renderer/store/hooks';
import { SIGNING_UP } from '../../../renderer/_reducers/_slices/signupSlice';
import { validateId } from '../../../components/validate/regex';
import { API_URL } from '../../../renderer/CONSTANT_URL';

export { IdStage };

function IdStage({ move, setMove }: { move: number; setMove: React.Dispatch<React.SetStateAction<number>> }) {
    const dispatch = useAppDispatch();

    const idInput = useRef<HTMLInputElement>(null);
    const [message, setMessage] = useState('');

    const handlePrev = () => {
        setMove((prev) => prev + 100);
        dispatch(SIGNING_UP(false));
    };

    const handleNext = () => {
        const element = idInput.current;

        // Ï†ïÍ∑úÏãù Í≤ÄÏÇ¨
        if (element !== null) {
            if (validateId(element.value) === true) {
                checkDuplicate(element.value);
            } else {
                setMessage('ÏòÅÎ¨∏, Ïà´Ïûê 20Ïûê Ïù¥ÎÇ¥Ïùò ÏïÑÏù¥ÎîîÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.');
            }
        }
    };

    // Ï§ëÎ≥µÍ≤ÄÏÇ¨
    async function checkDuplicate(id: string) {
        const res = await fetch(`${API_URL}/users/check-userId?userId=${id}`);
        const data = (await res.json()) as { duplicated: boolean; errorMessage?: string };

        if (data.duplicated === false) {
            setMessage('');
            setMove((prev) => prev - 100);
        } else {
            setMessage('Ïù¥ÎØ∏ Ï°¥Ïû¨ÌïòÎäî ÏïÑÏù¥ÎîîÏûÖÎãàÎã§.');
        }
    }

    return (
        <section className="signup-id-stage" style={move !== -100 ? { visibility: 'hidden' } : {}}>
            <h3 className="sr-only">ÏïÑÏù¥Îîî ÏûÖÎ†•</h3>
            <label htmlFor="signupId">ÏïÑÏù¥Îîî</label>
            <input
                type="text"
                id="signupId"
                placeholder="ÏòÅÎ¨∏, Ïà´Ïûê 6~20Ïûê Ïù¥ÎÇ¥"
                ref={idInput}
                minLength={6}
                maxLength={20}
                aria-describedby="desc-signup-id"
            />
            <em className="txt-signup-message" id="desc-signup-id">
                {message}
            </em>
            <div className="button-group-move">
                <button type="button" onClick={handlePrev} aria-label="Ïù¥Ï†Ñ ÌôîÎ©¥ÏúºÎ°ú Ïù¥Îèô">
                    Ï≤´ ÌôîÎ©¥
                </button>
                <button type="button" onClick={handleNext} aria-label="Îã§Ïùå ÌôîÎ©¥ÏúºÎ°ú Ïù¥Îèô">
                    ÎãâÎÑ§ÏûÑ
                </button>
            </div>
        </section>
    );
}
