import React, { useRef, useState } from 'react';
import { validateNickName } from '../../../components/validate/regex';
import { API_URL } from '../../../renderer/CONSTANT_URL';

export { NickNameStage };

function NickNameStage({ move, setMove }: { move: number; setMove: React.Dispatch<React.SetStateAction<number>> }) {
    const nickNameInput = useRef<HTMLInputElement>(null);
    const [message, setMessage] = useState('');

    const handlePrev = () => {
        setMove((prev) => prev + 100);
    };

    const handleNext = () => {
        const element = nickNameInput.current;

        // Ï†ïÍ∑úÏãù Í≤ÄÏÇ¨
        if (element !== null) {
            if (validateNickName(element.value) === true) {
                checkDuplicate(element.value);
            } else {
                setMessage('ÏòÅÎ¨∏, Ïà´Ïûê, ÌïúÍ∏Ä 17Ïûê Ïù¥ÎÇ¥Ïùò ÎãâÎÑ§ÏûÑÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.');
            }
        }
    };

    // Ï§ëÎ≥µÍ≤ÄÏÇ¨
    async function checkDuplicate(nickname: string) {
        const res = await fetch(`${API_URL}/users/check-nickname?nickname=${nickname}`);
        const data = (await res.json()) as { duplicated: boolean; errorMessage?: string };

        if (data.duplicated === false) {
            setMessage('');
            setMove((prev) => prev - 100);
        } else {
            setMessage('Ïù¥ÎØ∏ Ï°¥Ïû¨ÌïòÎäî ÎãâÎÑ§ÏûÑÏûÖÎãàÎã§.');
        }
    }

    return (
        <section className="signup-nickname-stage" style={move !== -200 ? { visibility: 'hidden' } : {}}>
            <h3 className="sr-only">ÎãâÎÑ§ÏûÑ ÏûÖÎ†•</h3>
            <label htmlFor="signupNickName">ÎãâÎÑ§ÏûÑ</label>
            <input
                type="text"
                id="signupNickName"
                ref={nickNameInput}
                placeholder="ÏòÅÎ¨∏, Ïà´Ïûê, ÌïúÍ∏Ä 17Ïûê Ïù¥ÎÇ¥"
                minLength={1}
                maxLength={17}
                aria-describedby="desc-signup-nickname"
            />
            <em className="txt-signup-message" id="desc-signup-nickname">
                {message}
            </em>
            <div className="button-group-move">
                <button type="button" onClick={handlePrev} aria-label="Ïù¥Ï†Ñ ÌôîÎ©¥ÏúºÎ°ú Ïù¥Îèô">
                    ÏïÑÏù¥Îîî
                </button>
                <button type="button" onClick={handleNext} aria-label="Îã§Ïùå ÌôîÎ©¥ÏúºÎ°ú Ïù¥Îèô">
                    ÎπÑÎ∞ÄÎ≤àÌò∏
                </button>
            </div>
        </section>
    );
}
